
import XInput from 'vux/src/components/x-input/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import UploadPhoto from '../../components/uploadPhoto.vue'
import {getParams} from '../../assets/js/util'
import api from '../../assets/js/api'
export default {
directives: {
TransferDom
},
components: {
XInput,
UploadPhoto,
Previewer
},
data () {
return {
isShowDesc: false,
isShowZh: false,
amount: 88,
desc: '',
list: [{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
},
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
}, {
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwwcynw2j20p00b4js9.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwwcynw2j20p00b4js9.jpg'
}],
order_id: getParams()['order_id'] || ''
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
chooseImg(){
/* 解决fastClick的延迟的bug */
this.$refs['uploadPhoto'].$el.click();
this.$refs['uploadPhoto'].$el.click();
},

getImgDataUrl(value){


},
uploadProgress(value){


},
uploadResData(value){
if (value.type == 'success') {
let img = new Image();
let that = this;
img.src = value.image_url;

img.onload = function () {

}
} else {

}
},
show (index) {
this.$refs.previewer.show(index)
},
deletePhoto (index){
this.list.splice(index, 1);
},
upload(){
if (!this.$refs.amountInput.valid) {
this.layer('请输入打款金额')
} else if (!this.list.length) {
this.layer('请上传打款凭证')
} else {
let imgs = []
this.list.forEach( val => {
imgs.push(val.src)
} )
this.showLoading('上传中')
this.$http.post(api.uploadVoucher,{
ordersn: this.order_id,
money: this.amount,
remark: this.desc,
voucher: imgs
}).then(res => {
this.hideLoading()
this.layer('上传凭证成功')
this.$router.push({
path: `/uploadsuc?order_id=${this.order_id}`
})
}).catch(e => {
this.hideLoading()
})
this.$router.push({
path: `/uploadsuc?order_id=${this.order_id}`
})
}
}
},
mounted() {
document.getElementsByTagName('title')[0].textContent = '上传凭证';
}
}



